}

.container {
  max-width: 990px;
  padding: 0 15px;
  margin: 0 auto;
}

.o-list-bare {
  list-style: none;
  margin: 0;
  padding: 0;
}

.two-col::after {
  clear: both;
  content: "";
  display: table;
}

@media only screen and (min-width: 768px) {
  .two-col__column {
    width: 50%;
    float: left;
    padding: 0 4rem;
  }
}

.about .icon-list {
  font-size: 2rem;
}

.about .icon-list__item.u-oneliner {
  font-size: 1.6rem;
}

@media only screen and (min-width: 480px) {
  .about .icon-list__item.u-oneliner {
    font-size: 2rem;
  }
}

.about__subheading {
  margin: 2rem 0;
}

@media only screen and (min-width: 768px) {
  .about__subheading {
    margin: 6rem 0 5rem;
  }
}

.about__copy p {
  line-height: 1.5;
  margin-bottom: 1.5em;
}

.about__pic {
  position: relative;
  max-width: 34rem;
  margin: 0 auto;
}

@media only screen and (min-width: 768px) {
  .about__pic {
    margin: 0;
  }
}

.about__pic img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about__pic__colorbar--top {
  max-width: 24rem;
}

@media only screen and (min-width: 768px) {
  .about__pic__colorbar--top {
    margin-left: -3rem;
  }
}

.about__pic__colorbar--bottom {
  float: right;
  width: 5rem;
}

.about__pic__colorbar--bottom-last {
  float: right;
  width: 8rem;
}

@media only screen and (min-width: 768px) {
  .about__pic__colorbar--bottom-last {
    margin-right: -3rem;
  }
}

.badge {
  position: relative;
  width: 9.4rem;
}

.badge::before {
  content: "";
  display: block;
  padding-bottom: 120%;
}

.badge__shield {
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  max-height: 100%;
  fill: transparent;
  stroke: currentColor;
  stroke-width: 1.4;
  stroke-linejoin: round;
  stroke-dasharray: 90;
  stroke-dashoffset: 97;
}

.badge__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40%;
  max-height: 100%;
  transform: translateX(-50%) translateY(-50%) scale(0.9);
}

.blockquote {
  color: #747474;
  margin: 5rem 0;
}

@media only screen and (min-width: 768px) {
  .blockquote {
    margin: 7rem 0;
  }
}

.blockquote__copy {
  font-size: 2rem;
  white-space: 0.5px;
  quotes: "“" "”" "‘" "’";
}

.blockquote__copy::before {
  content: open-quote;
  color: #0e2f56;
}

.blockquote__copy::after {
  content: close-quote;
  color: #ff304f;
}

@media only screen and (min-width: 768px) {
  .blockquote__copy {
    font-size: 2.4rem;
  }
}

.blockquote__cite {
  font-size: 1.4rem;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.blockquote__cite::before {
  content: "– ";
}

.bureau .icon-list {
  margin-left: 2rem;
}

@media only screen and (min-width: 768px) {
  .bureau .icon-list {
    margin-left: 4rem;
  }
}

.bureau__title {
  text-align: center;
}

.bureau__image {
  position: relative;
  overflow: hidden;
  margin: 1.5rem 0;
  cursor: pointer;
}

@media only screen and (min-width: 768px) {
  .bureau__image {
    margin: 4rem 0;
  }
}

.bureau__image::before {
  content: "";
  display: block;
  padding-bottom: 53%;
}

.bureau__image::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0.8;
  mix-blend-mode: color;
  background: inherit;
}

.bureau__image:hover > img {
  opacity: 0.2;
}

.bureau__image:hover .bureau__image__overlay {
  visibility: visible;
  opacity: 1;
}

.bureau__image--red {
  background: #ff304f;
}

.bureau__image--blue {
  background: #118df0;
}

.bureau__image--navy {
  background: #0e2f56;
}

.bureau__image > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: opacity .2s;
}

.bureau__image__overlay {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity .2s, visibility .2s;
}

.colorbar {
  display: flex;
  width: 100%;
}

.colorbar .colorbar__item:nth-child(1) {
  flex: 1 0 50%;
}

.colorbar .colorbar__item:nth-child(2) {
  flex: 1 0 22%;
}

.colorbar .colorbar__item:nth-child(3) {
  flex: 1 0 28%;
}

.colorbar__item {
  height: 1rem;
  flex: 1;
}

.colorbar__item--high {
  height: 6rem;
}

.dots {
  clear: both;
  margin: 8rem auto;
  display: flex;
  justify-content: center;
}

.dots__item {
  display: block;
  width: 0.9rem;
  height: 0.9rem;
  margin: 0 0.5rem;
}

.footer {
  margin: 5rem 0 0;
}

.footer__logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 10rem 0;
}

.footer__logos__item {
  display: flex;
  justify-content: center;
  align-items: center;
