  opacity: 1;
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}

.gray2color img {
  opacity: 0.3;
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
  transition: opacity .3s, filter .5s;
}

.header {
